import { _decorator, size, director } from "cc";
const { ccclass, property } = _decorator;

import BaseScene from "../BaseObject/BaseScene";
import AssetManager from "../DataManager/AssetManager";
import SlotManager from "../Prefabs/SlotManager";

@ccclass("SlotScene")
export default class SlotScene extends BaseScene {
  assetManager: AssetManager = null;
  slotManager: SlotManager = null;
  async onLoad() {
    super.onLoad();
    this.assetManager = AssetManager.getInstance();
    const assetResult = await this.assetManager.loadAssets();
    // console.log(this.assetManager);
    const slotManager = new SlotManager();
    const slotObject = slotManager.initSlotManager({
      rowNum: 1,
      columnNum: 3,
      slotSize: size(700, 500),
    });
    this.slotManager = slotManager;
    this.node.insertChild(slotObject, 10);
  }
  // // update (dt) {}
  buttonClicked(node, idx) {
    // this.slotManager.rollWithNetwork();
    this.slotManager.rollWithRandom();
    // this.slotManager.rollWithFixed([
    //   { iconKind: 2, backKind: 1 },
    //   { iconKind: 2, backKind: 2 },
    //   { iconKind: 2, backKind: 3 },
    // ]);
  }

  gotoMain() {
    director.loadScene("MainScene");
  }
}
